import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';

import { TokenParams } from '../auth/TokenParams';
import { AuthService } from '../auth/auth.service';
import { LocalStorageService } from '../auth/LocalStorageService';
import { UserInfo } from '../model/UserInfo';


@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['../../../assets/sass/login.scss', '../../../assets/sass/global.scss']
})

export class LoginComponent implements OnInit {

    loginData: any = {};
    tokenParam: TokenParams;
    constructor(private router: Router,
        private authService: AuthService,
        private localStorageService: LocalStorageService) { }

    title = 'CIS Academic Panning [B507-Dev]';
    login_error = false;
    userData: UserInfo;

    ngOnInit() {
    }

    login() {
        console.log(this.loginData.userName, this.loginData.password);
        this.authService.login(this.loginData.userName, this.loginData.password)
            .subscribe(data => {
                this.localStorageService.SetAutherizationData(data);
                this.authService.getUserInfo().subscribe(data1 => {
                    this.userData = data1;
                    if (this.userData.role === 'faculty') {
                        this.router.navigate(['/admin/report']);
                    } else {
                        this.router.navigate(['/welcome/planning']);
                    }
                });
            });
    }
}
