import { Component, OnInit } from '@angular/core';

import { StudentService } from '../student.service';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ICourse } from '../../model/ICourse';
import { ISemester } from '../../model/ISemester';
import { ModalComponent } from '../../modal/modal.component';

@Component({
  selector: 'app-review',
  templateUrl: './review.component.html',
  styleUrls: ['../../../../assets/sass/global.scss']
})
export class ReviewComponent implements OnInit {

  constructor(private studentService: StudentService, private modalService: NgbModal) { }

  courseList: Array<ISemester> = [];
  currentSemTab: ISemester;
  tabId: any;
  loading: boolean;
  termId: any;
  successMessage: any;

  ngOnInit() {
    this.loading = true; // Activate the Loading spin
    this.studentService.getMyPlan()
      .subscribe(
        data => {
          console.log(data);
          this.courseList = data;
          this.loading = false; // Deactivate the Loading spin

          this.showCourses(this.courseList[0].AcademicTerm.toString()); // To keep the first tab active
        }
      );
  }

  removeCourse(item: ICourse, index) {
    console.log(item);
    console.log(this.termId);
    this.studentService.removeCourse(this.termId, item.CurriculumID)
      .subscribe(
        data => {
          console.log(data);
          this.successMessage = data;
          const modalRef = this.modalService.open(ModalComponent);
          modalRef.componentInstance.message = this.successMessage.message;
          modalRef.componentInstance.headerMessage = 'Alert';
          this.currentSemTab.course.splice(index, 1);
          if (this.currentSemTab.course.length === 0) {
            const semIndex: number = this.courseList.indexOf(this.currentSemTab);
            this.courseList.splice(semIndex, 1);
          }
        },
        err => {
          console.log(err);
          const modalRef = this.modalService.open(ModalComponent);
          modalRef.componentInstance.message = err.error.message;
          modalRef.componentInstance.headerMessage = 'Alert';
        }
      );
  }
  showCourses(semId) {
    this.termId = semId;
    for (const element of this.courseList) {
      if (element.AcademicTerm === semId) {
        this.currentSemTab = element;
        this.tabId = semId;
        break;
      }
    }
  }

}
