import { Injectable } from '@angular/core';
import { TokenParams } from '../auth/TokenParams';

@Injectable()
export class LocalStorageService {
    public SetAutherizationData(auth: TokenParams): void {
        localStorage.setItem('Authorization', JSON.stringify(auth));
    }

    public GetAutherizationData(): TokenParams {
        const tokenData = JSON.parse(localStorage.getItem('Authorization'));
        return tokenData == null ? null : tokenData;
    }
    public RemoveAutherizationData(): void {
        localStorage.removeItem('Authorization');
    }

}
