import { Component, OnInit } from '@angular/core';

import { AdminService } from '../admin.service';
import { ISemester } from '../../model/ISemester';
import { Report } from '../../model/Report';
import { isNgTemplate } from '../../../../../node_modules/@angular/compiler';
import { forEach } from '../../../../../node_modules/@angular/router/src/utils/collection';
import { element } from '../../../../../node_modules/protractor';

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  styleUrls: ['./report.component.css']
})
export class ReportComponent implements OnInit {

  constructor(private adminService: AdminService) { }

  semesterData: any;
  selectedSemester: ISemester;
  reportData: any[] = [];
  values: string = '';
  public columnChartData: any;
  ngOnInit() {
    this.adminService.getSemestersInfo().subscribe(data => { this.semesterData = data; console.log(this.semesterData); });
  }

  getReport() {
    this.adminService.getReport(this.selectedSemester.AcademicTerm)
      .subscribe(data => {
        console.log(data);
        this.reportData = [];
        this.reportData.push(["CourseName", "Total"]);
        for (let element of data) {
          this.reportData.push([element[0], Number(element[1])]);
        }
        this.showReport(this.reportData);
      });
  }

  showReport(data) {
    console.log(data);
    this.columnChartData = {
      chartType: 'ColumnChart',
      dataTable: data,
      options: { title: 'Summery Report' }
    };
    console.log(this.columnChartData.dataTable);
  }

}
