import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpEvent, HttpInterceptor, HttpHandler, HttpRequest } from '@angular/common/http';

import { Observable } from 'rxjs';

import { LocalStorageService } from './LocalStorageService';


@Injectable()
export class AuthInterceptor implements HttpInterceptor {

    constructor(private localStorageService: LocalStorageService, private router: Router) { }

    // HttpInteceptor only support HttpClient request. It does not support Http Request
    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        // console.log(req);
        const tokenData = this.localStorageService.GetAutherizationData();
        if (tokenData === null) {
            this.router.navigate(['/login']);
        }
        const authHeader = 'Bearer ' + tokenData.access_token;

        const authreq = req.clone({ setHeaders: { Authorization: authHeader, 'Content-Type': 'application/json' } });

        return next.handle(authreq);
    }
}
