import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';


// import 'rxjs/add/observable/of';
// import 'rxjs/add/operator/catch';
// import 'rxjs/add/operator/map';

import { Headers, Http, HttpModule } from '@angular/http';
import { HttpHeaders } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import { TokenParams } from './TokenParams';

import { LocalStorageService } from './LocalStorageService';
import { environment } from '../../../environments/environment';
import { UserInfo } from '../model/userInfo';

@Injectable()
export class AuthService {
    AuthToken: '';
    constructor(private http: Http,
        private localStorageService: LocalStorageService,
        private httpClient: HttpClient,
    ) { }


    login(Username: string, Password: string): Observable<TokenParams> {
        const headerForTokenAPI = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' });
        const data = 'grant_type=password&username=' + Username + '&password=' + Password;
        return this.http.post(environment.localInternshipApi + 'api/login', data, { headers: headerForTokenAPI })
            .pipe(map((response: any) => response.json()));
    }

    getUserInfo(): Observable<UserInfo> {
        return this.httpClient.get<UserInfo>(environment.localInternshipApi + 'api/user');
    }

}

