import { Component, OnInit, Input } from '@angular/core';

import { StudentService } from '../student.service';
import { AngularWaitBarrier } from 'blocking-proxy/built/lib/angular_wait_barrier';
import { Data } from '@angular/router';
import { element } from 'protractor';
import { HttpErrorResponse } from '@angular/common/http';

import { ICourse } from '../../model/ICourse';
import { ISemester } from '../../model/ISemester';

import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalComponent } from '../../modal/modal.component';
import { content } from '../planning/content.component';
import { Success } from '../../model/Success';


@Component({
  selector: 'app-planning',
  templateUrl: './planning.component.html',
  styleUrls: ['../../../../assets/sass/global.scss']
})
export class PlanningComponent implements OnInit {

  constructor(private studnetService: StudentService, private modalService: NgbModal) { }
  semesterData: any;
  departmentData: any;
  courseData: Array<ICourse> = [];
  selectedDepartment: string;
  selectedCourse: ICourse;
  selectedSemester: ISemester;
  course: ICourse;
  semester: ISemester;
  myCourse: any[] = [];
  loading: boolean;
  successMessage: any;
  currentlevelTab: any = [];
  tabId: any;
  level: any = [];


  courseList: Array<ISemester> = [];

  ngOnInit() {
    this.studnetService.getSemestersInfo().subscribe(data => { this.semesterData = data; });
    this.studnetService.getDepartmentInfo().subscribe(data => { this.departmentData = data; console.log(data); });
  }

  getCourses() {
    if (this.selectedSemester !== undefined && this.selectedDepartment !== undefined) {
      this.loading = true;
      this.studnetService
        .getCourseInfo(this.selectedDepartment.substr(0, this.selectedDepartment.indexOf('-')).trim(), this.selectedSemester.AcademicTerm)
        .subscribe(data => {
          this.courseData = data;
          this.level = [];
          for (let i = 0; i < data.length; i++) {
            if (this.level.indexOf(data[i].CourseNumber.toString().substring(0, 1)) == -1) {
              this.level.push(data[i].CourseNumber.toString().substring(0, 1));
            }
          }
          this.level = this.level.sort();
          this.showCourses(this.level[0]);
          this.loading = false;
        });
    }
  }

  addCourse(item: ICourse, index) {
    console.log(index);
    if (item.MaxCredits !== item.MinCredits) {
      const modalRef = this.modalService.open(content, { backdropClass: 'light-blue-backdrop' });
      modalRef.componentInstance.MaxCredits = item.MaxCredits;
      modalRef.result.then((result) => {
        const NoOfCredits = result;
        this.saveCourse(this.selectedSemester.AcademicTerm, item.CurriculumID, NoOfCredits, index);
      });
    } else {
      this.saveCourse(this.selectedSemester.AcademicTerm, item.CurriculumID, item.MinCredits, index);
    }
  }


  saveCourse(AcademicTerm, cId, NumberOfCredits, index) {
    this.studnetService.savePlan(AcademicTerm, cId, NumberOfCredits)
      .subscribe(data => {
        console.log(data);
        this.successMessage = data;
        const modalRef = this.modalService.open(ModalComponent);
        modalRef.componentInstance.message = this.successMessage.message;
        modalRef.componentInstance.headerMessage = 'Alert';
        this.currentlevelTab[index].IsInMyPlan = true;
      },
        err => {
          console.log(err);
          const modalRef = this.modalService.open(ModalComponent);
          modalRef.componentInstance.message = err.error.message;
          modalRef.componentInstance.headerMessage = 'Alert';
        }
      );
  }
  showCourses(levelId) {
    this.currentlevelTab = [];
    for (let element of this.courseData) {
      if (element.CourseNumber.toString().substring(0, 1) == levelId) {
        this.currentlevelTab.push(element);
        this.tabId = levelId;
      }
    }
  }
}
