import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalComponent } from '../modal/modal.component';
import { AdminService  } from './admin.service';
import { LocalStorageService } from '../auth/LocalStorageService';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['../../../assets/sass/login.scss', '../../../assets/sass/global.scss']
})
export class AdminComponent implements OnInit {

  constructor(private adminService: AdminService,
    private modalService: NgbModal,
    private localStorage: LocalStorageService,
    private router: Router) { }

    userData: any;
    ngOnInit() {
        this.adminService.getUserInfo().subscribe(data => { this.userData = data; console.log(data); });
    }
    logout() {
      const modalRef = this.modalService.open(ModalComponent);
      modalRef.componentInstance.headerMessage = 'Logout';
      modalRef.componentInstance.message = 'Are You sure You want to logout?';
      modalRef.componentInstance.btnOkay = true;
      modalRef.result.then(result => {
          console.log(result);
          if (result === 'OK') {
              this.localStorage.RemoveAutherizationData();
              this.router.navigate(['/login']);
          }
      });
  }

}
