import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { Http, HttpModule } from '@angular/http';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { Ng2GoogleChartsModule } from 'ng2-google-charts';

import { AuthService } from './AcademicPlanning/auth/auth.service';
import { LocalStorageService } from './AcademicPlanning/auth/LocalStorageService';
import { StudentService } from './AcademicPlanning/student/student.service';
import { AdminService } from './AcademicPlanning/admin/admin.service';

import { AuthInterceptor } from './AcademicPlanning/auth/authInterceptor';

import { AppComponent } from './app.component';
import { LoginComponent } from './AcademicPlanning/Login/login.component';
import { WelcomeComponent } from './AcademicPlanning/student/welcome.component';
import { AuthComponent } from './AcademicPlanning/auth/auth.component';
import { ModalComponent } from './AcademicPlanning/modal/modal.component';
import { PlanningComponent } from './AcademicPlanning/student/planning/planning.component';
import { content } from './AcademicPlanning/student/planning/content.component';
import { ReviewComponent } from './AcademicPlanning/student/review/review.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { AdminComponent } from './AcademicPlanning/admin/admin.component';
import { ReportComponent } from './AcademicPlanning/admin/report/report.component';

const routes: Routes = [
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  {
    path: 'welcome', component: WelcomeComponent, children: [
      { path: 'planning', component: PlanningComponent },
      { path: 'review', component: ReviewComponent }
    ]
  },
  {
    path: 'admin', component: AdminComponent, children: [
      { path: 'report', component: ReportComponent }
    ]
  }
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    WelcomeComponent,
    AuthComponent,
    ModalComponent,
    PlanningComponent,
    ReviewComponent,
    content,
    AdminComponent,
    ReportComponent
  ],
  entryComponents: [ModalComponent, content],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    NgbModule.forRoot(),
    RouterModule.forRoot(routes, { useHash: true, enableTracing: false }),
    HttpClientModule,
    Ng2GoogleChartsModule
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true // we can have multiple inteceptors
    },
    AuthService,
    StudentService,
    AdminService,
    LocalStorageService],
  bootstrap: [AppComponent]
})
export class AppModule { }
