import { Injectable } from '@angular/core';
import { HttpClient, HttpClientModule, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';


import { AuthService } from '../auth/auth.service';
import { LocalStorageService } from '../auth/LocalStorageService';
import { StaticInjector } from '@angular/core/src/di/injector';
import { environment } from '../../../environments/environment';
import { ISemester } from '../model/ISemester';
import { ICourse } from '../model/ICourse';

@Injectable()
export class StudentService {
    constructor(private localStorageService: LocalStorageService,
        private httpclient: HttpClient) { }

    getUserInfo() {
        return this.httpclient.get(environment.localInternshipApi + 'api/user');
    }

    getSemestersInfo() {
        return this.httpclient.get(environment.localInternshipApi + 'api/planning/semester');
    }

    getCourseInfo(data: string, termId: string): Observable<Array<ICourse>> {
        return this.httpclient
            .get<Array<ICourse>>(environment.localInternshipApi + 'api/planning/course?area=' + data + '&AcademicTerm=' + termId);
    }

    getDepartmentInfo() {
        return this.httpclient.get(environment.localInternshipApi + 'api/planning/department');
    }

    savePlan(termId: string, cId: string, NumberOfCredits: number) {
        const data = JSON.stringify({
            'termId': termId,
            'cId': cId,
            'NumberOfCredits': NumberOfCredits
        });
        // console.log(data);
        return this.httpclient.post(environment.localInternshipApi + 'api/planning/addtoplan', data);
    }

    getMyPlan(): Observable<Array<ISemester>> {
        return this.httpclient.get<Array<ISemester>>(environment.localInternshipApi + 'api/planning/myPlan');
    }

    removeCourse(termId: string, cId: string) {
        const data = JSON.stringify({
            'termId': termId,
            'cId': cId
        });
        return this.httpclient.post(environment.localInternshipApi + 'api/planning/removeCourse', data);
    }

}

